import React, { Component} from 'react'
import Header from '../components/header'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import './play.css'
import kriti from '../imgs/kriti.mp4';
import circe from '../imgs/circe.jpeg';

class Play extends Component {
    render() {
        return (
            <div className='body'>
                <Header></Header>
                <div className='bodyHeader' style={{position:'relative',top:'125px'}}>
                    <p>PLAY</p>
                    <p style={{fontSize:'35px', letterSpacing:'0px', fontWeight:'lighter'}}>Just For Fun</p>
                </div>
                
                <div>
                <Row className='overview' > 
                    <Col md = {5} style={{marginTop:'13%', marginLeft:'5%'}}>
                        <video loop style={video} autoPlay controls onPlay={()=>{new Event("video_play")}}>
                            <source src={kriti}/>
                        </video>
                    </Col>
                    <Col md = {5} style={{marginTop:'15%', marginLeft:'5%'}}>
                    <p>I love giving my friends Valentine's cards every year and didn't want to let the pandemic stop that annual tradition. So, I created cards on Adobe XD and animated them with things that each friend likes. This friend is a Tenet fan so I imitated the movie poster in her card.</p>
                    </Col>
                    </Row>
                    <Row className='overview' > 
                    <Col md = {5} style={{marginLeft:'5%'}}>
                    <img src={circe} alt='Circe' clasName='persimg' style={{ marginTop:'30px', overflow:'hidden',  boxShadow: '13px 13px 20px #FFCAB9'}}/>

                    </Col>
                    <Col md = {5} style={{marginTop:'13%', marginLeft:'5%'}}>
                    <p>My most recent read was Circe by Madeline Miller. I loved this book for its beautiful prose, feminist retelling of a classic Greek myth, and subtle but applicable life lessons. 10/10 would recommend!</p>
                    </Col>
                    </Row>
                    </div>
            </div>
        )
    }
}

const video = {
    width: '90%', 
    boxShadow: '0 0 50px 0 rgba(0,0,0,0.35)',
    borderRadius: '6px',
    backgroundColor:'#F8F8F8',
    marginLeft:'5%',
}

export default Play;